.navigation {
  background-color: white;
  padding: 10px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
.navigation-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navigation-logo {
}
.navigation-buttons {
}
.navigation-dropdown {
  display: none;
}
.dropbtn {
  background-color: white;
  color: black;
  font-size: 30px;
  border: none;
  cursor: pointer;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  right: 0;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {
  background-color: #f1f1f1;
}

.dropdown:hover .dropdown-content {
  display: block;
}
@media (max-width: 770px) {
  .navigation-buttons {
    display: none;
  }
  .navigation-dropdown {
    display: block;
  }
}
